import React from "react";
import "./AboutHero.css";
import heroimg from "../../../images/rewe_von_auen.jpg";

const AboutHero = () => {
  return (
    <div class="hero">
      <img class="hero-img" src={heroimg} alt="Rewe Filiale von Auen" />
      <div className="hero-text-content">
        <div className="container">
          <div className="hero-text-content-center">
            <h1>ber Uns</h1>
          </div>
        </div>
      </div>
    </div>
  );
};
export default AboutHero;
